<template>
  <v-card>
      <v-card-title >
          Assign Customers To Agent   <small class="grey--text mx-3"> ({{items.length}} items selected  ) </small>
          <v-spacer></v-spacer>
          <v-icon @click="$emit('close')">mdi-close</v-icon>
      </v-card-title>
      <v-divider class="mb-4"></v-divider>
     <v-card-text class="py-1">
          <v-data-footer 
          :options="options" 
          :pagination="pagination" 
          @update:options="updateOptions"></v-data-footer>

      </v-card-text>

      <v-card-text style="height: 300px;" class="pa-0">

          <v-data-table 
          hide-default-footer 
          fixed-header
          height="300px"
          :page="page"
          :items-per-page="limit"
          :items="items" 
          :headers="headers"
          @update:options="options = $event"
          @pagination="pagination = $event" >
              <template v-slot:item.action="{ item }">
                  <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                          <v-btn  v-bind="attrs"
                          v-on="on" icon small color="red white--text" @click="$emit('unselect',item)"> <v-icon size="20">mdi-delete</v-icon> </v-btn>
                      </template>
                      <span>Remove</span>
                  </v-tooltip>
              </template>
          </v-data-table>
      </v-card-text>
      <v-divider class="mt-1 mb-4"></v-divider>

      <v-card-text class="pt-1">
          <app-autocomplete  v-bind="autocomplete.bind" v-model="autocomplete.value"  ></app-autocomplete>
      </v-card-text>
      <v-divider class="mb-6"></v-divider>
      <v-card-actions >
          <v-btn  color="green white--text" :disabled="Boolean(!autocomplete.value)" @click="$emit('save', autocomplete.value)"> save</v-btn>
          <v-btn  @click="$emit('close')"> cancel</v-btn>
      </v-card-actions>
    </v-card>
</template>

<script>
import AppAutocomplete from '../../Autocomplete'

export default {
    name:'ItemsDialogAssign',
    props: {
      items: Array,
      headers: Array
    },
    components: {
        AppAutocomplete
    },
    data () {
        return {
            page: 1,
            limit: 10,
            options: {},
            pagination: {},
            autocomplete: {
                value: '',
                bind: {
                    label: 'Owner',
                    placeholder:'Select owner to assign the selected item to it',
                    collection: 'user',
                    template: 'user',
                    itemText:'displayName',
                    itemValue: '_id',
                    prependInnerIcon: "mdi-account",
                    outlined: true,
                    hideDetails: true,
                    hideAddNew: true,
                    returnObject: true,
                    dense: true,
                    or: false,
                    query: {
                        role: [ '!wakel', '!affiliate'],
                        depositType: 'rtn'
                        
                    }
                }
            }
        }
    },
    methods: {
      updateOptions (options) {
        console.log(options);
        this.page = options.page
        this.limit = options.itemsPerPage
        console.log(options);
      }
    } 
}
</script>

